import React from 'react';
import ReactDOM from 'react-dom';
import web from '../src/images/hero-img.png';
import { NavLink } from 'react-router-dom';

const Common = (props) => {
    return(
        <>
           <section>
               <div className='container'>
                   <div className='row'>
                           <div className='col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column'>
                               <h1> {props.name} </h1>
                               <h2 className='my-3'> We are team of talented developer making webiste</h2>
                               <div>
                                   <NavLink to={props.visit} className='btn btn-primary'> {props.btnname} </NavLink>
                               </div>
                           </div>
                           <div className='col-md-6 order-1 order-lg-2 d-flex justify-content-center'>
                               <img src={props.imgsrc} alt='' className='img-fluid'/>
                           </div>
                   </div>
               </div>
           </section>
        </>
    );
};

export default Common;