import React from 'react';
import ReactDOM from 'react-dom';
import web from '../src/images/hero-img.png';
import Common from './Common';

const Home = () => {
    return(
        <>
          <Common
              name='Welcome to home  page'
              imgsrc={web} 
              visit='/service' 
              btnname='Contact Now'
            />
        </>
    );
};

export default Home;