import React from 'react';
import ReactDOM from 'react-dom';
import web from '../src/images/about.png';
import Common from './Common';

const About = () => {
    return(
        <>
            <Common
              name='Welcome to about page'
              imgsrc={web} 
              visit='/contact' 
              btnname='Contact Now'
            />
        </>
    );
};

export default About;